import Modal from "./components/ModalTemplate.vue";
import MaskedInput from "vue-masked-input";
import axios from "axios";

export default {
  name: "App",
  components: {
    Modal,
    MaskedInput,
  },
  props: {},
  data() {
    return {
      showModal: false,
      selected: "1",
      errors: [],
      username: null,
      phone: null,
      email: null,
      message: '',
      contacts: [
        {
          id: 1,
          name: "Москва",
        },
        {
          id: 2,
          name: "Санкт-Петербург",
        },
        {
          id: 3,
          name: "Казань",
        },
      ],
    };
  },

  methods: {
    checkForm: function (e) {
      this.errors = [];

      if (!this.username) {
        this.errors.push("Обязательное поле");
      }
      if (!this.phone) {
        this.errors.push("Обязательное поле");
      }
      if (!this.email) {
        this.errors.push("Обязательное поле");
      } else if (!this.validEmail(this.email)) {
        this.errors.push("Укажите корректный адрес электронной почты.");
      }

      e.preventDefault();

      if (this.username && this.phone && this.email) {
        let tocall = this.phone.match(/\+|\d+/g).join(""),
          send_data = {
            name: this.username,
            phone: tocall,
            email: this.email,
            city_id: this.selected,
          };
        axios.post("http://hh.autodrive-agency.ru/test-tasks/front/task-7/", { ...send_data }).then(response => {
          self.message = 'Data is entered'
          console.log(response);
        }).catch(error => {
             console.log('error:', error)
          });

        this.showModal = false;
        this.username = this.phone = this.email = "";
      }
    },
    validEmail: function (email) {
      var re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
  },
};
